@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,400&display=swap");

:root {
  --primary-color: #9900ff;
  --secondary-color: #6c757d;
  --dark-color: #000000;
  --light-contrast: #f2f2f267;
}

.app {
  /* text-align: center;*/
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
}

.main-nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.main-nav-list {
  list-style-type: none;
  display: flex;
  padding: 0;
}

.nav-item {
  margin: auto;
  cursor: pointer;
  color: #333;
  transition: transform 0.2s ease-in-out;
}

.nav-item:last-child {
  margin-right: 0;
  color: #9900ff;
}

.nav-item:hover {
  opacity: 0.8;
  color: #9900ff;
  transform: scale(1.1);
}

.logo {
  font-size: 24px;
  font-weight: bold;
  line-height: 1;
  margin-right: auto;
  flex-shrink: 0;
  color: #536878;
  /* border: 2px solid;
  border-radius: 0.5rem; */
  padding: 5px;
  animation: floating 3s ease-in-out infinite;
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
}
@keyframes floating {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(0);
  }
}

.emoji {
  font-size: 1.3rem;
  line-height: 1rem;
  margin-left: 0.5rem;
}

.content {
  padding: 20px;
}

.hero {
  background-color: #f2f2f2;
  margin-top: 1rem;
  border-radius: 0 0 5px 5px;
  text-align: center;
  height: 80vh;
}

.hero-content {
  max-width: 600px;
  margin: 40px auto;
}

.hero-title {
  font-size: 36px;
  color: #333;
  margin-bottom: 15px;
  background: linear-gradient(to left, #d48723, #9900ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero-subtitle {
  font-size: 18px;
  line-height: 1.5;
  color: #666;
  padding: 1.5rem;
  margin: 0 1.5rem;
}

.hero-button {
  margin-top: 1rem;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #9900ff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.hero-button:hover {
  background-color: #8000cc;
}

